//-----------function declaration--------------------
export function title(block){
    return `
    <div class="row">
    <div class="col-sm">
        <h1>${block.value}</h1>
    </div>
    </div>
    `;
}
//-----------------------------------------------
export function text(block){
    return `
    <div class="row">
        <div class="col-sm">
            <p>${block.value}</p>
        </div>
    </div>
    `;
}

export function columns(block){
    let columns='';
        
        for(let i =0;i<block.value.length;i++){
            columns+=`
            <div class="col-sm">
                ${block.value[i]}
            </div>
        `;
            
        }
        return `<div class="row">
                ${columns}
                </div>`;
        
}
//--------------------------------------------------------
export function image(block){
    return `
        <div class="row">
        <img src="${block.value}"/>
        </div>
    `
}

//altertanive code

export function colummnsEach(block){
    let html=''
    block.value.forEach(item =>{
        html+=`
        <div class="col-sm">
                ${item}
            </div>
        
        `;
        
    });
    return `<div class="row">
                ${html}
                </div>`;

}

export function colummnsMap(block){
    let html= block.value.map(item => `<div class="col-sm">
            ${item}
        </div>`);
    return `<div class="row">
        ${html.join('')}
        </div>`;   
}